@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
/*@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap");*/

* {
    margin: 0;
    padding: 0;
}

.doughnut-chart-container {
    width: 500px;
    height: 600px;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#chartjs-tooltip {
    text-align: center;
    border-radius: 50%;
    /*width: 210px;*/
    /*height: 210px;*/
    /*margin: 15px;*/
    border: solid 2px black;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    color: white;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#chartjs-tooltip * {
    padding: 0;
    margin: 0;

}

#chartjs-tooltip h3 {
    font: 25px "SF Pro Display";
    margin: 25px;
    /*margin-bottom: 25px;*/
    /*margin-top: 30px;*/
}

#chartjs-tooltip p {
    margin-bottom: 15px;
}

.tooltip-percent {
    font: 20px "SF Pro Text";

}

.tooltip-amount {
    font: 15px "SF Pro Text"
}

